.js30-content .panels {
  min-height: 70vh;
  overflow: hidden;
  display: flex;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .js30-content .panels {
    min-height: 500px;
  }
}
.js30-content .panel {
  background: #6B0F9C;
  box-shadow: inset 0 0 0 5px rgba(255, 255, 255, 0.1);
  color: white;
  text-align: center;
  align-items: center;
  transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
  font-size: 20px;
  background-size: cover;
  background-position: center;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .js30-content .panel {
    font-size: 16px;
  }
}
.js30-content .panel1 {
  background-image: url("/build/images/portfolio/other/Iulia_Living%20Room-5.jpeg");
}
.js30-content .panel2 {
  background-image: url("/build/images/portfolio/other/Iulia_Living%20Room-6.jpeg");
}
.js30-content .panel3 {
  background-image: url("/build/images/portfolio/other/Iulia_Living%20Room-9.jpeg");
}
.js30-content .panel4 {
  background-image: url("/build/images/portfolio/other/Bathroom-226.jpg");
}
.js30-content .panel5 {
  background-image: url("/build/images/portfolio/other/David_Living%20and%20Dining%20Room-233.jpeg");
}
.js30-content .panel > * {
  margin: 0;
  width: 100%;
  transition: transform 0.5s;
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
}
.js30-content .panel > *:first-child {
  transform: translateY(-100%);
}
.js30-content .panel > *:last-child {
  transform: translateY(100%);
}
.js30-content .panel.active > *:first-child {
  transform: translateY(0%);
}
.js30-content .panel.active > *:last-child {
  transform: translateY(0%);
}
.js30-content .panel p {
  font-family: "Raleway", sans-serif;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
  font-size: 21px;
}
@media screen and (max-width: 767px) {
  .js30-content .panel p {
    font-size: 14px;
  }
}
.js30-content .panel p:nth-child(2) {
  font-size: 35px;
}
@media screen and (max-width: 767px) {
  .js30-content .panel p:nth-child(2) {
    font-size: 20px;
  }
}
.js30-content .panel.open {
  font-size: 40px;
  flex: 5;
}
@media screen and (max-width: 767px) {
  .js30-content .panel.open {
    font-size: 20px;
  }
}